body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  background-image: url(/Weather-app/static/media/clouds.3e018142.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  font-family: "Roboto";
}

.wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 80%;
  max-width: 800px;
  min-width: 300px;
  height: 90vh;
  background-color: rgba(0, 0, 0, .6);
}
.f-form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.f-input {
  margin: 4vh 0;
  background-color: rgba(67, 47, 245, 0.404);
  width: 100%;
  font-size: 20px;
  color: rgb(200, 206, 224);
  padding: 5px;
  border: none;
  border-radius: 4px;
  box-shadow: 0 0 8px 3px black;
}

.date {
  font-size: 20px;
  margin: 10px 20px;
  color: white;
  text-shadow: 4px 4px 5px grey;
}
.result,
.values,
.t-w-p,
.sun,
.sunrise,
.sunset {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.result {
  width: 90%;
  height: 80%;
  background-color: rgba(0, 0, 0, 0.84);
}

.city {
  text-align: center;
  margin: 0 auto 30px;
  font-size: 30px;
  padding: 5px 0;
  color: rgb(233, 224, 224);
  text-shadow: 0 0 1px black, 0 0 2px white;
}

.t-w-p {
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 20px 15px;
  padding: 10px;
  width: 150px;
  height: 120px;
  font-size: 21px;
  background-image: radial-gradient(circle,
    rgb(92, 144, 204),
    rgb(16, 16, 199));
  border-radius: 14px;
  box-shadow: 0 0 1px 1px rgb(59, 46, 247), 0 0 2px 3px rgb(0, 0, 0);
}

.t-w-p i,
.sun i {
  color: rgb(227, 239, 243);
  font-size: 32px;
  padding: 5px;
  text-shadow: 0 2px 4px black;
}

.sun {
  position: relative;
  margin-top: 30px;
}

.sunrise,
.sunset {
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  padding: 25px;
  margin: 10px;
  font-size: 20px;
  background-image: radial-gradient(circle, rgb(133, 73, 245), rgb(41, 2, 114));
  border-radius: 10px;
  box-shadow: 0 0 1px 1px rgb(92, 6, 133), 0 0 2px 3px rgb(0, 0, 0);
}

.error {
  text-align: center;
  font-size: 30px;
  color: rgb(182, 5, 5);
  font-weight: bold;
}
@media (max-width: 600px) and (orientation: portrait) {
  .wrap {
    width: 96%;
    height: 98vh;
    padding-top: 10vh;
  }

  .f-form {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .f-input {
    margin: 0;
  }

  .result {
    width: 96%;
  }

  .city {
    margin: 0 auto 5px;
    font-size: 28px;
  }

  .values,
  .sun {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }

  .t-w-p,
  .sunrise,
  .sunset {
    width: 40%;
    font-size: 19px;
    padding: 5px;
  }

  .t-w-p {
    width: 130px;
    height: 100px;
    margin: 10px;
  }

  .sun {
    margin-top: 5px;
    padding: 5px;
  }

  .t-w-p i,
  .sun i {
    font-size: 30px;
  }
}

@media (max-width: 333px) and (orientation: portrait) {
  .wrap {
    padding-top: 6vh;
  }

  .t-w-p {
    width: 110px;
    height: 90px;
  }

  .t-w-p,
  .sunrise,
  .sunset {
    font-size: 17px;
    padding: 5px;
    margin: 5px;
  }

  .t-w-p i,
  .sun i {
    font-size: 20px;
  }
}

@media (max-width: 1020px) and (orientation: landscape) {
  .wrap {
    width: 97%;
    height: 96vh;
    padding-top: 7vh;
  }

  .f-input {
    margin: 10px auto;
    font-size: 18px;
  }

  .result {
    width: 96%;
    padding: 5px 10px 10px;
  }

  .city {
    margin: 5px auto 0;
    font-size: 20px;
  }

  .t-w-p,
  .sunrise,
  .sunset {
    font-size: 17px;
    padding: 7px;
    margin: 15px;
  }

  .t-w-p {
    width: 140px;
    height: 90px;
  }

  .sun {
    margin-top: 5px;
  }
}

@media (max-width: 600px) and (orientation: landscape) {
  .t-w-p,
  .sunrise,
  .sunset {
    font-size: 15px;
    padding: 5px;
    margin: 15px;
  }

  .t-w-p i,
  .sunrise i,
  .sunset i {
    font-size: 20px;
  }

  .t-w-p {
    width: 120px;
    height: 70px;
  }
}

